  margin: 0;
}
html,body{
  height: 100%;
}
body{
  background-color: #121212;
  font-family: "Poppins",sans-serif;
  color: #fff;
}

img,svg{
  display: block;
  max-width: 100%;
}
.swiper-slide{
  width: auto !important;
}
